.form {
    background-color: #272c2d;
    background-image: linear-gradient(to left, #1dafe5 0%, #8ac34c 100%); }

.form__wrapper {
    display: flex;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    p {
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 16px;
        text-align: left;
        // Text style for "Сделайте з"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        max-width: 128px;
        width: 100%;
        margin-right: 80px; }
    p.form__tel {
        max-width: 300px;
        width: 100%;
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 30px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 40px;
        text-align: right;
        text-transform: uppercase;
        // Text style for "+ 38 (063)"
        font-style: normal;
        letter-spacing: -0.75px;
        line-height: normal;
        position: relative;
        &::before {
            position: absolute;
            left: -30px;
            top: 0;
            content: url(../img/Forma_1_copy.png); }
        margin-right: 44px; }
    span {
        max-width: 120px;
        width: 100%;
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13px;
        text-align: left;
        // Text style for "Или оставь"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        margin-right: 30px; }
    input[type="text"] {
        padding-left: 20px;
        width: 237px;
        height: 40px;
        background-color: #ffffff;
        color: #000000;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13px;
        text-align: left;
        // Text style for "Ваш телефо"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        border: none;
        margin-right: 20px; }
    input.btn {
        width: 191px;
        height: 41px;
        border: none;
        cursor: pointer !important; } }
