.contact {
    background-color: #f7f7f7;
    h2 {
        margin-bottom: 10px; }
    padding-top: 50px;
    padding-bottom: 50px;
    margin-bottom: 50px; }

.contact__wrapper {
    max-width: 765px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }

.contact__profile {
    max-width: 300px;
    width: 100%;
    display: flex;
    flex-direction: column; }

.contact__profile__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }

.contact__profile__img {
    max-width: 110px;
    width: 100%;
    img {
        display: block;
        width: 100%; } }

.contact__profile__text {
    display: flex;
    flex-direction: column;
    h5 {
        color: #33312f;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        text-align: left;
        // Text style for "Елена Петр"
        font-family: "RobotoBold";
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: 24px; }
    p {
        color: #33312f;
        font-size: 14px;
        font-family: "Roboto";
        font-weight: 400;
        font-style: normal;
        letter-spacing: 0.14px;
        line-height: 18px;
        margin-bottom: 8px; }
    span {
        color: #87c250;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Другие обь"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        margin-bottom: 10px; }
    a {
        color: #373737;
        font-family: 'Roboto';
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "+ 38 (063)"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        &:hover {
            text-decoration: underline; } }
    p.online, {
        font-size: 14px;
        font-family: "Roboto";
        font-weight: 400;
        font-style: normal;
        letter-spacing: 0.14px;
        line-height: 18px;
        color: #bcbcbc;
        span {
            color: #bcbcbc; } } }

.contact__profile__links {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
        color: #33312f;
        font-family: 'RobotoMedium';
        font-size: 20px;
        font-weight: 500;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        font-style: normal;
        letter-spacing: 0.2px;
        line-height: normal; } }

.contact__profile__links__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 150px;
    width: 100%; }

.contact__form {
    max-width: 440px;
    width: 100%;
    input[type="text"] {
        padding-left: 20px;
        height: 70px;
        width: 100%;
        border-radius: 3px;
        border: 1px solid #dddddd;
        background-color: #ffffff;
        color: #33312f;
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13px;
        text-align: left;
        // Text style for "Ваше сообщ"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        margin-bottom: 10px; } }

.contact__form__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    input[type="submit"] {
        border: none;
        cursor: pointer;
        max-width: 142px;
        width: 100%; } }

.contact__form__inner__file {
    label {
        position: relative;
        cursor: pointer;
        &::before {
            position: absolute;
            top: 1px;
            left: -25px;
            content: url(../img/clip.png); } }
    input {
        display: none; }
    p {
        color: #33312f;
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        text-align: left;
        font-family: "RobotoBold";
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: 24px;
 }        // Text style for "jpg, jpeg,"
    span {
        color: #33312f;
        font-family: Roboto;
        font-size: 14px;
        font-family: "Roboto";
        font-weight: 400;
        font-style: normal;
        letter-spacing: 0.14px;
        line-height: 18px; } }



.contact__form__links {
    padding-left: 30px;
    margin-top: 33px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 233px;
    width: 100%;
    a {
        position: relative;
        color: #373737;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 30px;
        text-align: left;
        // Text style for "Cкачать"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal;
        &:first-child {
            &::before {
                position: absolute;
                top: -3px;
                left: -30px;
                content: url(../img/printer2.png); } }
        &:last-child {
            &::before {
                position: absolute;
                top: 0;
                left: -20px;
                content: url(../img/download.png); } } } }
