.location {
    h2 {
        margin-bottom: 20px; }
    margin-bottom: 50px; }

.location__wrapper {
    max-width: 730px;
    width: 100%;
    p {
        color: #33312f;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Бизнес-цен"
        font-style: normal;
        letter-spacing: 0.14px;
        line-height: normal; }
    iframe {
        width: 100%;
        height: 235px; } }

.location__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 40px; }

.location__inner__left {
    display: flex;
    flex-direction: column;
    max-width: 280px;
    width: 100%; }

.location__inner__left__top, .location__inner__left__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 25px;
    margin-bottom: 10px;
    p {
        color: #373737;
        font-size: 16px;
        font-style: normal;
        letter-spacing: normal;
        line-height: 26.47px;
        text-align: left;
        // Text style for "Т, еатраль"
        font-family: "Roboto";
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        position: relative; }
    span {
        color: #373737;
        font-size: 16px;
        font-style: normal;
        letter-spacing: normal;
        line-height: 26.47px;
        text-align: left;
        font-family: "RobotoItalic";
        font-style: italic;
        letter-spacing: normal;
        line-height: normal; } }


.location__inner__left__top {
    p {
        &:first-child {
            &::before {
                position: absolute;
                top: 0;
                left: -25px;
                content: url(../img/m.png); } }
        &:last-child {
            &::before {
                position: absolute;
                top: 0;
                left: -20px;
                content: url(../img/relaxing-walk.png); } } } }
.location__inner__left__bottom {
    p {
        &:first-child {
            &::before {
                position: absolute;
                top: 0;
                left: -25px;
                content: url(../img/m2.png); } }
        &:last-child {
            &::before {
                position: absolute;
                top: 0;
                left: -20px;
                content: url(../img/relaxing-walk.png); } } }
    margin-bottom: 0; }

.location__inner__right {
    max-width: 336px;
    width: 100%;
    position: relative;
    &::before {
        position: absolute;
        top: 5px;
        left: -60px;
        content: url(../img/car.png); }
    p {
        margin-bottom: 10px;
        color: #33312f;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "20 мин на"
        font-style: normal;
        letter-spacing: 0.14px;
        line-height: normal;
        &:last-child {
            margin-bottom: 0; } } }
