.sub__header {
    position: relative;
    padding-top: 5px;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        background-image: linear-gradient(to right, #87c250 0%, #65ade0 100%);
        height: 4px;
        content: ""; } }

.sub__header__links {
    ul {
        display: flex; }
    li {
        margin-left: 4px;
        a {
            color: #c3c3c3;
            font-family: 'Roboto';
            font-size: 14px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            line-height: 30px;
            text-align: left; }
        &:last-child {
            a {
                font-size: 14px;
                color: #33312f;
                font-family: "RobotoItalic";
                font-style: italic;
                letter-spacing: 0.14px;
                line-height: normal;
                font-style: normal;
                letter-spacing: normal;
                line-height: 30px; } } } }
