* {
    box-sizing: border-box;
    outline: none; }

body, html {
    background: white;
    color: $fontColor;
    font-family: $font-family;
    font-size: 16px;
    min-width: 320px;
    max-width: 100%;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
.page-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // min-height: 100vh
    max-width: 1440px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden; }
.container {
    max-width: 1115px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include r($wide) {
        padding-left: 15px;
        padding-right: 15px; } }

h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
    padding: 0;
    margin: 0; }
a {
    color: $fontColor;
    text-decoration: none;
    font-family: $font-family;
    font-size: 18px;
    font-weight: 400;
    &:hover, &:focus {
        text-decoration: none; } }
button {
    padding: 0;
    border: 0;
    box-shadow: none;
    background: none;
    cursor: pointer;
    &:focus {
        outline: none; } }
ul {
    list-style: none; }
ol {
    list-style: decimal; }
img.svg {
    display: none; }
.swiper-button-next, .swiper-button-prev {
    transform: none;
    margin: 0;
    padding: 0;
    top: auto;
    bottom: auto;
    left: auto;
    right: auto;
    background: none;
    color: transparent;
    &::after {
        display: none; } }


.btn {
    width: 285px;
    height: 45px;
    border-radius: 3px;
    background-color: #fc6120;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 24px;
    text-align: left;
    // Text style for "Назначить"
    font-style: normal;
    letter-spacing: 0.16px;
    line-height: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
    &:hover {
        opacity: 0.8; } }

.section__title {
    color: #373737;
    font-family: Roboto;
    font-size: 24px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 51.09px;
    text-align: left;
    // Text style for "Описание"
    font-style: normal;
    letter-spacing: 0.14px;
    line-height: normal;
    margin-bottom: 20px; }
