.suggestions {
    h2 {
        text-align: center;
        margin-bottom: 20px; } }

.suggestions__wrapper {
    display: flex;
    flex-direction: column; }

.suggestions__sort {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 310px;
    width: 100%;
    margin-bottom: 30px;
    p {
        color: #000000;
        font-size: 14px;
        line-height: 24px;
        text-align: left;
        font-family: "RobotoLight";
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }
.suggestions__sort__links {
    max-width: 190px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
        font-size: 14px;
        line-height: 24px;
        font-family: "RobotoBold";
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; }
    a {
        &:first-child {
            color: #86c351;
            text-decoration: underline; } } }

.suggestions__slide {
    border-radius: 3px;
    max-width: 272px;
    width: 100%;
    border: 1px solid #dddddd;
    background-color: #ffffff; }


.suggestions__slide__top {
    width: 100%;
    height: 182px;
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: flex-end;
    padding: 10px;
    p {
        color: #ffffff;
        font-family: 'RobotoBold';
        font-size: 14px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 18.12px;
        text-align: left;
        // Text style for "Н, азвание"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; }
    span {
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 18.12px;
        text-align: right;
        // Text style for "02.03.21"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        position: absolute;
        top: 10px;
        right: 10px; } }

.suggestions__slide__mid {
    padding: 10px; }

.suggestions__slide__mid__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
        font-family: 'Roboto';
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Тип:"
        color: #373737;
        span {
            color: #9bc258; } } }

.suggestions__slide__mid__title__count {
    width: 36px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    img {
        width: 15px;
        height: 15px; }
    p {
        color: #272c2d;
        font-family: 'Roboto';
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 18.12px;
        text-align: right;
        // Text style for "15"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }
