.sidebar {
    display: flex;
    flex-direction: column;
    max-width: 286px;
    width: 100%; }

.sidebar__manager {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 10px; }

.sidebar__manager__img {
    max-width: 77px;
    width: 100%;
    border-radius: 3px;
    background-color: #33312f;
    img {
        width: 100%;
        display: block; } }

.sidebar__manager__text {
    display: flex;
    flex-direction: column;
    h5 {
        color: #33312f;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        text-align: left;
        // Text style for "Елена Петр"
        font-family: "RobotoBold";
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: 24px; }
    p {
        color: #33312f;
        font-size: 14px;
        font-family: "Roboto";
        font-weight: 400;
        font-style: normal;
        letter-spacing: 0.14px;
        line-height: 18px; }
    span {
        color: #87c250;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Другие обь"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; }
    a {
        color: #373737;
        font-family: 'Roboto';
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "+ 38 (063)"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        &:hover {
            text-decoration: underline; } } }


.sidebar__serv {
    display: flex;
    flex-direction: column; }

.sidebar__serv__item {
    width: 100%;
    max-width: 286px;
    img {
        width: 100%; }
    margin-bottom: 50px; }

.sidebar__form {
    width: 100%;
    margin-bottom: 30px;
    input[type="text"] {
        width: 100%;
        height: 43px;
        border-radius: 3px;
        border: 1px solid #dddddd;
        background-color: #ffffff;
        color: #a1a3a4;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13px;
        text-align: left;
        // Text style for "Дата"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        margin-bottom: 10px;
        padding-left: 20px; }
    input[type="submit"] {
        border: none; } }

.sidebar__form__selects {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    select {
        width: 137px;
        height: 40px;
        border-radius: 3px;
        border: 1px solid #dddddd;
        background-color: #ffffff;
        padding-left: 20px;
        color: #a1a3a4;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13px;
        text-align: left;
        // Text style for "Дата"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background: url(../img/v.png) no-repeat;
        background-position-x: calc(100% - 10px);
        background-position-y: center; } }
