.services {
    h2 {
        text-align: center; }
    margin-bottom: 50px; }

.services__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    a {
        border-radius: 5px;
        max-width: 350px;
        width: 100%;
        height: 195px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        padding-bottom: 20px;
        padding-left: 20px;
        background-size: cover;
        background-repeat: no-repeat;
        margin-bottom: 31px;
        transition: 0.7s;
        &:hover {
            opacity: 0.8; } }
    p {
        text-shadow: 1px 1px 6px #000000;
        color: #ffffff;
        font-family: 'RobotoBold';
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Дизайн и р"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal; } }
