.tenants {
    h2 {
        margin-bottom: 20px; }
    margin-bottom: 50px; }

.tenants__wrapper {
    border: 1px solid #e8e8e8; }

.tenants__top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    p {
        &:first-child {
            width: 35%; }
        &:last-child {
            width: 65%; } }
    p {
        color: #33312f;
        font-family: 'RobotoBold';
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Этаж"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal; }
    padding-left: 20px;
    background-color: #f1f5f5; }

.tenants__inner__item {
    display: flex;
    padding-left: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #e8e8e8;
    p {
        &:first-child {
            width: 35%; }
        &:last-child {
            width: 65%; } }
    p {
        color: #33312f;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Duane Read"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal; } }
