.foto__gallery {
    margin-bottom: 40px; }

.foto__gallery__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 30px; }

.foto__gallery__wrapper {
    display: flex;
    flex-direction: column;
    max-width: 730px;
    width: 100%; }

.foto__gallery__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }

.foto__gallery__inner__left {
    a {
        max-width: 361px;
        width: 100%;
        display: block;
        border-radius: 5px;
        img {
            width: 100%;
            display: block; } } }

.foto__gallery__inner__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    max-width: 362px;
    width: 100%; }

.foto__gallery__inner__img__wrapper {
    max-width: 177px;
    width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
    img {
        width: 100%;
        display: block; } }

.foto__gallery__inner__invis {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    display: none; }
