.intro {
  height: 619px;
  position: relative;
  .container {
    position: relative;
    z-index: 3;
    height: 100%; }
  &::before {
    position: absolute;
    bottom: -4px;
    left: 0;
    content: url(../img/Layer_33.png);
    z-index: 2; } }

.slider-container {
  position: absolute;
  left: 0;
  width: 1140px;
  margin-left: auto;
  margin-right: auto; }

.intro__find-reslut {
  position: absolute;
  left: 20px;
  top: 10px;
  display: flex;
  align-items: center;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  font-style: italic;
  padding: 7px 12px;
  z-index: 10;
  border: 1px solid rgba(#fff, .3);
  border-radius: 3px;
  svg {
    fill: #fff;
    width: 23px;
    height: 15px;
    margin-right: 10px; } }

.swiper-slide {
  background-size: cover;
  background-position: center; }

.gallery-top {
  width: 100%;
  height: 100%; }


.gallery-thumbs {
  position: relative;
  height: 66px;
  max-width: 475px;
  width: 100%; }

.gallery-thumbs .swiper-slide {
  height: 100%; }

.gallery-item {
  width: 87px;
  display: block;
  margin: 0 auto;
  border: 1px solid transparent;
  position: relative;
  border-radius: 3px;
  overflow: hidden;
  cursor: pointer;
  img {
    width: 100%;
    height: auto;
    display: block; } }



.gallery-thumbs .swiper-slide-thumb-active {
  opacity: 1;
  .gallery-item {
    border: 2px solid #02aee5;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: #02aee5;
      opacity: .2; }
    .view {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      fill: #ffffff;
      width: 27px;
      height: 22px; } } }


.slider-button-next,.slider-button-prev {
  position: absolute;
  left: 50px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  cursor: pointer;
  svg {
    width: 32px;
    height: 28px;
    fill: #fff; }
  &.swiper-button-disabled {
    opacity: .3; } }



.slider-button-next {
  left: auto;
  right: 50px;
  svg {
    transform: rotate(180deg); } }



.intro__wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 40px;
  justify-content: flex-end; }

.intro__inner__top {
  margin-bottom: 130px;
  h1 {
    color: #ffffff;
    font-family: 'RobotoBold';
    font-size: 60px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 51.09px;
    text-align: left;
    // Text style for "БЦ Славия"
    font-style: normal;
    letter-spacing: 0.35px;
    line-height: normal; }
  p {
    color: #ffffff;
    font-family: 'RobotoMedium';
    font-size: 18px;
    font-weight: 500;
    font-style: normal;
    letter-spacing: normal;
    line-height: 30px;
    text-align: left;
    // Text style for "Бизнес-цен"
    font-style: normal;
    letter-spacing: 0.18px;
    line-height: normal;
    text-align: center; } }

.intro__inner__top__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 284px;
  width: 100%; }

.intro__inner__top__main {
  p,a {
    color: #ffffff;
    font-family: 'RobotoLight';
    font-size: 16px;
    font-weight: 300;
    font-style: normal;
    letter-spacing: normal;
    line-height: 30px;
    text-align: left; }
  a {
    text-decoration: underline; } }

.intro__inner__top__links {
  max-width: 46px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: 2;
  top: 30px;
  right: 40px; }

.intro__inner__bottom {
  margin-top: 20px;
  max-width: 475px;
  width: 100%;
  ul {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  a {
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 18px;
    text-align: left;
    // Text style for "Описание"
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    border-bottom: 1px dotted #fff;
    span {
      color: #0cb0d8; } } }
