.sub-intro {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    margin-bottom: 70px; }

.sub-intro__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.sub-intro__address, .sub-intro__price, .sub-intro__office {
    display: flex;
    height: 80px; }

.sub-intro__address {
    max-width: 350px;
    width: 100%;
    padding-left: 40px;
    align-items: center;
    p {
        max-width: 191px;
        width: 100%;
        color: #373737;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Улица Моск"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal; }
    position: relative;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    &::before {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        content: url(../img/pin.png); } }

.sub-intro__office {
    display: flex;
    justify-content: center;
    align-items: center; }

.sub-intro__office__wrapper {
    display: flex;
    flex-direction: column;
    max-width: 410px;
    width: 100%;
    span {
        color: #a1a3a4;
        font-family: 'Roboto';
        font-size: 12px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13px;
        text-align: left;
        // Text style for "Свободные"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }

.sub-intro__office__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 330px;
    width: 100%;
    p {
        color: #272c2d;
        font-family: 'RobotoLight';
        font-size: 30px;
        font-weight: 300;
        font-style: normal;
        letter-spacing: normal;
        line-height: 35px;
        text-align: center;
        // Text style for "1, 5"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }


.sub-intro__office__inner__items {
    display: flex;
    align-items: center;
    p {
        margin-left: 6px;
        border-radius: 3px;
        padding: 5px;
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 35px;
        text-align: center;
        // Text style for "1, 25 m, 2"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        background-color: #21b0d6;
        &:nth-child(2) {
            background-color: #74c063; }
        &:nth-child(3) {
            background-color: #74c063; }
        &:nth-child(4) {
            border: 1px solid #4ab996;
            background-color: #ffffff;
            color: #000000;
            font-family: 'RobotoItalic';
            font-size: 12px;
            font-style: italic;
            letter-spacing: normal;
            line-height: 35px;
            text-align: center;
            // Text style for "В, се офис"
            font-style: normal;
            letter-spacing: normal;
            line-height: normal; } } }

.sub-intro__price {
    max-width: 350px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 36px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    span {
        color: #a1a3a4;
        font-family: 'Roboto';
        font-size: 12px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 13.4px;
        text-align: left;
        // Text style for "Начальная"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; }
    p {
        color: #272c2d;
        font-family: 'Roboto';
        font-style: normal;
        letter-spacing: normal;
        line-height: 20.62px;
        text-align: left;
        font-size: 31px;
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        span {
            color: #272c2d;
            font-family: 'RobotoLight';
            font-size: 18px;
            font-style: normal;
            letter-spacing: normal;
            line-height: normal;
 } } }            // Text style for "10++"

