.center__features {
    margin-bottom: 60px; }

.center__features__wrapper, .center__features__wrapper__invisible {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }


.center__features__wrapper__invisible {
    display: none;
    margin-bottom: 15px; }

.center__features__inner {
    li {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
        position: relative;
        padding-left: 17px;
        &::before {
            position: absolute;
            top: 5px;
            left: 0px;
            width: 6px;
            height: 6px;
            background-image: linear-gradient(86deg, #87c250 0%, #65ade0 100%);
            border-radius: 100%;
            content: ""; }
        p {
            color: #33312f;
            font-size: 14px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            line-height: 18px;
            text-align: left;
            // Text style for "Статус стр"
            font-family: "RobotoBold";
            font-weight: 700;
            font-style: normal;
            letter-spacing: 0.14px;
            line-height: normal; }
        span {
            font-size: 14px;
            color: #33312f;
            font-family: 'Roboto';
            font-weight: 400;
            font-style: normal;
            letter-spacing: 0.14px;
            line-height: normal; } } }
