.content {
    span.learn-more {
        cursor: pointer;
        position: relative;
        color: #21b0d6;
        font-family: 'RobotoBold';
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 25px;
        text-align: center;
        // Text style for "Показать е"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        &::before {
            position: absolute;
            right: -25px;
            top: 0;
            content: url(../img/right-arrow.png); } } }


.content__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }

.content__wrapper__inner {
    display: flex;
    flex-direction: column; }
