.free__offices {
    margin-bottom: 50px; }

.free__offices__data {
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-start;
    border: 1px solid #e8e8e8;
    background-color: #f1f5f5;
    padding: 20px; }

.free__offices__data__inner {
    position: relative;
    display: flex;
    max-width: 533px;
    width: 100%;
    justify-content: space-between;
    align-items: center; }

.free__offices__data__item {
    display: flex;
    align-items: center;
    color: #33312f;
    font-family: 'RobotoBold';
    font-size: 16px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 24px;
    text-align: left;
    // Text style for "Цена, $/m2"
    font-style: normal;
    letter-spacing: 0.16px;
    line-height: normal;
    position: relative;
    &:nth-child(2) {
        &:hover {
            .free__offices__data__inner__price {
                display: flex; } } }
    &:nth-child(5) {
        &:hover {
            .free__offices__data__inner__price {
                display: flex; } } }
    img {
        width: 10px;
        height: 7px;
        margin-left: 5px; } }

.free__offices__data__inner__price {
    border: 1px solid #e8e8e8;
    position: absolute;
    top: 100%;
    right: 0;
    padding: 10px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    display: none;
    a {
        white-space: nowrap;
        margin-bottom: 5px;
        color: #33312f;
        font-family: 'RobotoBold';
        font-size: 16px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Цена, $/m2"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal; } }

.free__offices__wrapper {
    display: flex;
    flex-direction: column; }

.free__offices__inner {
    width: 100%;
    box-shadow: 0 3px 24px rgba(0, 0, 0, 0.11);
    background-color: #ffffff;
    margin-bottom: 10px; }

.free__offices__top {
    padding: 11px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    border: 1px solid #e8e8e8; }

.active {
    .free__offices__top {
        padding: 11px 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-image: linear-gradient(86deg, #87c250 0%, #65ade0 100%);
        border: none; }
    .free__offices__top__text {
        p {
            color: #fdfbfb; } }
    .free__offices__top__like {
        svg {
            width: 18px;
            height: 17px;
            fill: #fff;
            opacity: 1; } }
    .free__offices__top__closed {
        svg {
            &:first-child {
                display: none;
                fill: #fff;
                opacity: 1; }
            &:last-child {
                display: block;
                fill: #fff;
                opacity: 1; } } }
    .free__offices__inner__content {
        border: 2px solid #87c250;
        display: block;
        border-top: none; } }

.free__offices__top__text {
    max-width: 540px;
    width: 100%;
    padding-left: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
        color: #33312f;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "2 эт"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal; } }

.free__offices__top__like {
    svg {
        width: 18px;
        height: 17px;
        fill: #000;
        opacity: 0.17; } }

.free__offices__top__closed {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
        transform: rotate(180deg);
        width: 14px;
        height: 14px;
        fill: #000;
        opacity: 0.17; } }

.free__offices__top__closed {
    svg {
        &:first-child {
            display: block;
            // fill: #fff
 }            // opacity: 1
        &:last-child {
            display: none; } } }


.free__offices__inner__content {
    max-width: 730px;
    width: 100%;
    padding: 20px 60px;
    display: none; }

.free__offices__inner__content__slider {
    position: relative; }

.swiper-container.free__offices__slider__container {
    margin-bottom: 20px; }

.free__offices__slide {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px; }

.swiper-button-next.free__offices__button-next {
    top: 50%;
    right: 0;
    transform: translateY(-50%); }

.swiper-button-prev.free__offices__button-prev {
    top: 50%;
    left: 0;
    transform: translateY(-50%); }

.free__offices__slide__img {
    max-width: 255px;
    width: 100%;
    img {
        max-height: 160px;
        width: auto; } }

.free__offices__inner__content__text {
    padding-top: 20px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    p {
        padding-left: 22px;
        position: relative;
        color: #33312f;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 24px;
        text-align: left;
        // Text style for "Открытое п"
        font-style: normal;
        letter-spacing: 0.16px;
        line-height: normal;
        &::before {
            width: 6px;
            height: 6px;
            position: absolute;
            left: 0;
            border-radius: 100%;
            top: 4px;
            background-image: linear-gradient(86deg, #87c250 0%, #65ade0 100%);
            content: ""; } } }

.free__offices__inner__content__more {
    display: flex;
    justify-content: flex-end;
    a {
        width: 83px;
        height: 25px;
        border-radius: 3px;
        border: 1px solid #4ab996;
        background-color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #000000;
        font-family: 'RobotoItalic';
        font-size: 12px;
        font-style: italic;
        letter-spacing: normal;
        line-height: 35px;
        text-align: center;
        // Text style for "П, одробне"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }
