.footer {
    background-color: #272c2d;
    padding-top: 50px;
    padding-bottom: 50px; }


.footer__main {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 35px; }

.footer__main__left {
    max-width: 400px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    li {
        margin-bottom: 10px; }
    a {
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 34px;
        text-align: left;
        // Text style for "Главная ,"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        &:hover {
            text-decoration: underline; } } }

.footer__main__right {
    max-width: 540px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }

.footer__main__bussines {
    max-width: 200px;
    width: 100%;
    display: flex;
    flex-direction: column;
    p {
        opacity: 0.5;
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 15px;
        text-align: left;
        // Text style for "Бизнес-цен"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        margin-bottom: 15px; } }

.footer__main__bussines__btn {
    width: 191px;
    height: 25px;
    border-radius: 3px;
    border: 1px solid #4ab996;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    font-family: 'Roboto';
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 12px;
    text-align: left;
    // Text style for "Добавить б"
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    position: relative;
    &::before {
        position: absolute;
        top: 5px;
        left: 11px;
        content: url(../img/+.png); } }

.footer__main__bussines__col {
    max-width: 200px;
    width: 100%;
    margin-bottom: 15px;
    ul {
        column-count: 2; }
    li {
        margin-bottom: 10px; }
    a {
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 30px;
        text-align: left;
        // Text style for "Класс «ОСЗ"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        &:hover {
            text-decoration: underline; } } }

.footer__main__contact {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    a.footer__main__contact__tel {
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 40px;
        text-align: left;
        // Text style for "+38 (044)"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        // Text style for "6"
        font-style: normal;
        // letter-spacing: -1.8px
        line-height: normal;
        margin-bottom: 5px;
        &:hover {
            text-decoration: underline; } }
    .footer__main__contact__mail {
        color: #4ab996;
        font-family: 'Roboto';
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 40px;
        text-align: left;
        // Text style for "info@allbc"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }


.footer__main__contact__input {
    position: relative;
    margin-bottom: 60px;
    input {
        width: 257px;
        height: 30px;
        border-radius: 3px;
        border: 1px solid rgba(255, 255, 255, 0.2);
        background-color: #000000;
        opacity: 0.4;
        color: #ffffff;
        font-family: Roboto;
        font-size: 12px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 30px;
        text-align: left;
        // Text style for "Поиск по с"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal;
        padding-left: 15px; }
    &::before {
        position: absolute;
        top: 5px;
        right: 10px;
        content: url(../img/Search_copy.png); } }


.footer__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.footer__license {
    p {
        opacity: 0.5;
        color: #ffffff;
        font-family: Roboto;
        font-size: 13px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 15px;
        text-align: left;
        // Text style for "© Allbc.in"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }

.footer__links {
    max-width: 260px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
        opacity: 0.4;
        color: #ffffff;
        font-family: Roboto;
        font-size: 11px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 9px;
        text-align: left;
        // Text style for "Design by"
        font-style: normal;
        letter-spacing: normal;
        line-height: normal; } }

.footer__links__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 107px;
    width: 100%; }
